<script lang="ts">
	// Proxcryptor.svelte
	import { onMount, createEventDispatcher } from 'svelte';
	import { validatePubKey } from '$lib/utils/index';

	export let rootCID = null;
	export let proxcryptor;
	export let ipfsNode; // pass in an instance of ipfs

	//  onSubmitted gets bound to, and overwritten by active component
	export let onSubmitted = () => {}; // optional callback by child component to run after tx submitted

	const dispatch = createEventDispatcher();

	let joseCryptor;
	let setJoseCryptor;
	let timeoutIDs = new Map();
	let loaded;

	onMount(async () => {
		const { DagJoseCryptor } = await import('./dagjosecryptor');

		setJoseCryptor = async () => {
			joseCryptor = new DagJoseCryptor(ipfsNode, proxcryptor, rootCID); //refesh when updated
			loaded = true;
		};
	});

	let decrypt = async (tagNode) => await joseCryptor.selfDecrypt(tagNode);

	async function decryptTagNode(tag) {
		if (!rootCID || !ipfsNode || !tag) return;

		try {
			const cid = (await ipfsNode.dag.get(rootCID, { path: `/${tag}`, localResolve: true })).value;
			let tagNode = (await ipfsNode.dag.get(cid, { localResolve: true })).value;
			return await decrypt(tagNode);
		} catch (error) {
			// tag may not exist yet, or maybe the user is typing
			console.warn(`${tag} no DAG data`);
		}
	}

	// TODO: Save spinner
	const handleTagChange = async function (event) {
		// load contacts in p2p network
		if (!event.detail.tag && event.detail.contacts) dispatch('loadContacts', event.detail.contacts);

		let invalid =
			!event.detail || !event.detail.tag || (!event.detail.props && !event.detail.targetPublicKey);

		if (invalid) return; // TODO: Handle missing vals better

		// does this event add to the access list, or change the property values?
		if (event.detail.hasOwnProperty('targetPublicKey'))
			setAccess(event.detail.tag, event.detail.targetPublicKey);
		else if (event.detail.hasOwnProperty('tag') && event.detail.hasOwnProperty('props'))
			putValue({ tag: event.detail.tag, value: event.detail.props });

		// if tag = Contacts, then pass along to PiperNet for P2P updates
		if (event.detail.tag === 'Contacts') {
			dispatch('updateContacts', event.detail.props.props);
		}
	};

	function putValue({ tag, value }) {
		if (timeoutIDs.has(tag)) clearTimeout(timeoutIDs.get(tag)); // cancel any exisitng waiting

		timeoutIDs.set(
			tag,
			setTimeout(async () => {
				timeoutIDs.delete(tag);

				let schema = '';
				console.log('Put: ', { tag, value });

				await joseCryptor.put(value, tag, schema);
				joseCryptor = joseCryptor; // refresh UI?

				onSubmitted(); // update selected component
			}, 5000)
		);
	}

	$: if (proxcryptor && setJoseCryptor) setJoseCryptor();

	// update the rootCID as req'd
	$: if (joseCryptor && joseCryptor.rootCID && handleRootCIDUpdate) handleRootCIDUpdate();

	function handleRootCIDUpdate() {
		dispatch('rootCID', joseCryptor.rootCID);
	}

	// save access function to a svelte store so it can be used by any component, any time
	// allows access to tag by someone's PublicKey
	async function setAccess(tag: string, targetPublicKey: string) {
		console.log(`"Setting access to ${tag} for ${targetPublicKey}"`);
		const pubKeyBytes = validatePubKey(targetPublicKey);
		console.log({ pubKeyBytes });
		if (!pubKeyBytes) return new Error('Public Key not valid');
		// allow access to this tag by this public key
		await joseCryptor.setTagReKeys(tag, pubKeyBytes);
		joseCryptor = joseCryptor; // triggers this.rootCID to fire
	}
</script>

{#if joseCryptor}
	{#if loaded}
		<slot {handleTagChange} {decryptTagNode} />
	{/if}
{:else}
	Log into a wallet to encrypt your data/.
{/if}
