<script>
	/**
	 * Loads, saves, updates, and displays the latest rootCID
	 */
	import { onMount } from 'svelte';
	import { CID } from 'multiformats/cid';
	// import getTagNodes from utils
	import GetTags from './templates/Contacts/GetTags.svelte';
	import { Progressbar } from 'flowbite-svelte';
	import { tweened } from 'svelte/motion';
	import { cubicOut } from 'svelte/easing';
	// allows users to pass ina  rootCID as propoerty
	export let rootCID = null;

	let ipfsNode;

	let tagNodes;
	let handleRootCIDUpdate;
	let loaded; // let this Component check for saved previous rootCID before loading anything in the slot

	const ROOT_CID = '__ROOT_CID__'; // Database key

	let start = Date.now();

	// create tweened value between 0 and 20 seconds
	const loader = tweened(1, {
		duration: 45000,
		easing: cubicOut
	});

	async function loadIPFS() {
		// setup IPFS
		const IPFSmodule = await import('../modules/ipfs-core-0.14.0/ipfs-core.js');
		const IPFS = IPFSmodule.default;

		if (globalThis.ipfsNode) {
			return;
		}
		console.log('Creating IPFS node...');

		ipfsNode = await IPFS.create();
		globalThis.ipfsNode = ipfsNode;

		const identity = await globalThis.ipfsNode.id();
		let nodeId = identity.id;

		console.info('NodeId', nodeId, `Loaded in ${(Date.now() - start) / 1000}s`);
	}

	onMount(async () => {
		$loader = 99;
		await loadIPFS();

		const { ImmortalDB } = await import('immortal-db');

		if (!rootCID) {
			const res = await ImmortalDB.get(ROOT_CID);

			if (res) {
				rootCID = CID.asCID(res) || CID.parse(res);
			}
		}

		handleRootCIDUpdate = async () => {
			if (!rootCID || !CID.isCID(rootCID)) return;
			// refresh the tag nodes
			const root = await ipfsNode.dag.get(rootCID);
			tagNodes = root.value;
			await ImmortalDB.set(ROOT_CID, rootCID.toString());
		};

		loaded = true;

		return () => {
			globalThis.ipfsNode.stop();
			globalThis.ipfsNode = null;
		};
	});

	$: if (rootCID && ipfsNode) {
		handleRootCIDUpdate();
	}
</script>

<div>
	{#if rootCID}
		RootCID: <span class="font-mono">
			{rootCID.toString().substring(0, 6) +
				'...' +
				rootCID.toString().substring(rootCID.toString().length - 6, rootCID?.toString().length)}
			<a href="https://dweb.link/api/v0/dag/get?arg={rootCID}" target="_blank">DWeb</a>
			| <a href="https://explore.ipld.io/#/explore/{rootCID}" target="_blank">Explorer</a></span
		>
	{/if}

	<!-- 
    <GetTags {rootCID} let:tagNode>
		Click to view: {tagNode.tag}
	</GetTags> 
    -->
	{#if tagNodes}
		<div class="m-4 p-2 border-slate-300 bg-slate-200 shadow-sm rounded-lg">
			<ul>
				{#each [...Object.entries(tagNodes)] as [tag, cid]}
					<li>
						<div class="my-1 py-1">
							<span class="bg-green-600 text-white rounded p-2 mr-6">{tag}: </span><a
								href="https://explore.ipld.io/#/explore/{cid}"
								target="_blank">{cid}</a
							>
						</div>
					</li>
				{/each}
			</ul>
		</div>
	{/if}
</div>

{#if loaded}
	<!-- show the selected tag in the slot  -->
	<slot {tagNodes} {ipfsNode} />
{:else if $loader}
	<div class="p-6">
		<Progressbar
			size="h-6"
			progress={String($loader.toFixed(0))}
			labelOutside="Loading... {$loader.toFixed(0)}%"
		/>
	</div>
{/if}
